import React from 'react';
import Dropdown from 'react-bootstrap/Dropdown';

const Rule = (props) => {
    const localRandomPercent = parseInt(props.localRandomPercent);
    const globalRandomPercent = parseInt(props.globalRandomPercent);
    const playerName = props.playerName;
    const cardIndex = props.cardIndex;
    const cardValue = ['Ace', 'Two', 'Three', 'Four', 'Five', 'Six', 'Seven', 'Eight', 'Nine', 'Ten', 'Jack', 'Queen', 'King'][cardIndex % 13];
    const complicatedWords = [
        { word: "vibrato", meaning: "A musical technique" },
        { word: "kerning", meaning: "The spacing between letters" },
        { word: "halyard", meaning: "A type of rope used on boats" },
        { word: "enrapture", meaning: "To make happy" },
        { word: "apicoectomy", meaning: "A type of tooth surgery" },
        { word: "pentadecagon", meaning: "A shape" },
        { word: "wyvern", meaning: "A type of dragon" },
        { word: "amiatina", meaning: "A breed of donkey" },
        { word: "mangalica", meaning: "A breed of pig" },
        { word: "bannock", meaning: "A type of bread" },
        { word: "chapi-chapi", meaning: "A type of kite" },
        { word: "bichon frise", meaning: "A breed of dog" },
        { word: "lapis lazuli", meaning: "A blue rock" },
        { word: "apiary", meaning: "A place for bees" },
        { word: "sauropod", meaning: "A type of dinosaur" },
        { word: "xylem", meaning: "A part of a plant" },
        { word: "mote", meaning: "A speck" },
        { word: "arborist", meaning: "A tree surgeon" },
        { word: "manicotti", meaning: "A type of pasta" },
        { word: "scapula", meaning: "A type of bone" },
        { word: "ramekin", meaning: "A type of bowl" },
        { word: "muntjac", meaning: "A type of deer" },
        { word: "mauve", meaning: "A shade of purple" },
        { word: "pulsar", meaning: "A type of star" },
        { word: "catamaran", meaning: "A type of boat" },
    ]
    const complicatedWord = complicatedWords[globalRandomPercent % complicatedWords.length];
    const skills = [
        'singing', 'dancing', 'hosting', 'cooking',
        'running', 'quizzing', 'video gaming', 'driving',
        'their job', 'DIY', 'drawing', 'fashion',
        'yoga', 'bowling', 'ice skating', 'puzzles'
    ]
    const rules = {
        'Ace': {
            'maxRandomPercents': [50, 80, 100],
            'titles': ['Place', 'Poker Face', 'Touch Base'],
            'rules': [
                `${playerName} picks a place they've been to. Anyone who's not been there drinks.`,
                `${playerName} tells a lie and a truth. Everyone guesses which is which and drinks if they get it wrong.`,
                `${playerName} sends a kind message to someone they've not spoken to in a while. Or they drink.`,
            ]
        },
        'Two': {
            'maxRandomPercents': [80, 100],
            'titles': ['Never Did I Do', 'you'],
            'rules': [
                `Everyone says something they've never done (e.g. skiing). Anyone who has done that thing drinks.`,
                `${playerName} chooses a player. The chosen player drinks.`
            ]
        },
        'Three': {
            'maxRandomPercents': [50, 70, 85, 100],
            'titles': ['Agree', 'Bee', 'Decree', 'Vitamin C'],
            'rules': [
                `${playerName} says a controversial statement (e.g. tea is better than coffee). The side fewest players agree with drinks.`,
                `Anyone who seeing a bee at the end of this sentence drinks. ${localRandomPercent > 50 ? '🐝' : ''}`,
                `${playerName} decides if everybody take a drink or nobody does.`,
                `The person who most recently ate a citrus fruit, takes a drink.`
            ]
        },
        'Four': {
            'maxRandomPercents': [30, 80, 90, 100],
            'titles': ['Senor', 'Superior', 'Saw', 'Guffaw'],
            'rules': [
                `Males drink.`,
                `${playerName} picks the player they think is the best at ${skills[globalRandomPercent % skills.length]}. Everyone votes agree or disagree. The losing side drinks`,
                `${playerName} picks a movie they've seen. Anyone who's not seen it drinks.`,
                `${playerName} tells a joke (or drinks). Any other player who finds the joke funny drinks.`
            ]
        },
        'Five': {
            'maxRandomPercents': [100],
            'titles': ['Press Master'],
            'rules': [
                `Next time ${playerName} puts their chosen body part (e.g. thumb) on the ground/table/webcam, the last one to copy them drinks.`
            ]
        },
        'Six': {
            'maxRandomPercents': [30, 50, 100],
            'titles': ['Chicks', 'Party Tricks', 'Picks'],
            'rules': [
                `Females drink.`,
                `${playerName} demonstrates one of their party tricks, or takes a drink.`,
                `${playerName} picks someone to drink.`
            ]
        },
        'Seven': {
            'maxRandomPercents': [80, 100],
            'titles': ['Heaven', 'Confession'],
            'rules': [
                `Last one pointing to the sky drinks.`,
                `${playerName} says a band/artist they like as a guilty pleasure, or takes a drink.`
            ]
        },
        'Eight': {
            'maxRandomPercents': [100],
            'titles': ['Mate'],
            'rules': [
                `${playerName} picks someone who will drink every time they do this game.`
            ]
        },
        'Nine': {
            'maxRandomPercents': [80, 100],
            'titles': ['Mine', 'Punch Line'],
            'rules': [
                `${playerName} picks a thing they own (e.g. guitar). Anyone who doesn't own one drinks.`,
                `${playerName} tells a joke (or drinks). Any other player who finds the joke funny drinks.`
            ]
        },
        'Ten': {
            'maxRandomPercents': [100],
            'titles': ['Categories'],
            'rules': [
                `${playerName} picks a category (e.g. counties). Round the circle give examples of the category. First to fail drinks.`
            ]
        },
        'Jack': {
            'maxRandomPercents': [70, 100],
            'titles': ['Quack', 'Snack'],
            'rules': [
                `Anyone seeing a duck at the end of this sentence drinks. ${localRandomPercent > 50 ? '🦆' : ''}`,
                `${playerName} picks a snack they ate this week. Anyone who's never tried that snack drinks.`
            ]
        },
        'Queen': {
            'maxRandomPercents': [80, 90, 100],
            'titles': ['Mean', 'Clean', 'Machine'],
            'rules': [
                `Anyone who doesn't know what ${complicatedWord.word} means, takes a drink.`,
                `The person who bathed/showered most recently takes a drink.`,
                `${playerName} dances like a robot for ten seconds or drinks.`
            ]
        },
        'King': {
            'maxRandomPercents': [80, 100],
            'titles': ['Sing', 'Bling'],
            'rules': [
                `The group picks a song. ${playerName} can sing the chorus or take a drink.`,
                `Everybody wearing jewellery drinks.`
            ]
        },
    }
    const cardRules = rules[cardValue];
    let ruleIndex = 0;
    for (; ruleIndex < cardRules.maxRandomPercents.length; ruleIndex++) {
        if (globalRandomPercent <= cardRules.maxRandomPercents[ruleIndex]) {
            break;
        }
    }
    const title = cardRules.titles[ruleIndex];
    const rule = cardRules.rules[ruleIndex];
    return (
        <div style={{ textAlign: "center", padding: "0", border: "solid 2px rgba(0,0,0,0)", borderRadius: "5px" }} >
            <h4>{playerName} got a{['Ace', 'Eight'].includes(cardValue) ? 'n' : ''} {cardValue}</h4>
            {cardValue} is <strong>{title}</strong>: {rule}

            {title === 'Mean'
                &&
                <Dropdown style={{ marginTop: "20px" }}>
                    <Dropdown.Toggle variant="outline-light" id="definition-reveal-dropdown" style={{ width: "100%" }}>
                        Click to reveal definition
                        </Dropdown.Toggle>
                    <Dropdown.Menu style={{ width: "100%" }}>
                        <Dropdown.Header style={{ color: "black" }}>
                            <strong>{complicatedWord.word}</strong><br />{complicatedWord.meaning}
                        </Dropdown.Header>
                    </Dropdown.Menu>
                </Dropdown>
            }
        </div>
    )
}
export default Rule;