import React from 'react';
import Player from './Player';

const PlayerList = (props) => {
    return (
        <ol className="PlayerList">
            {
                props.players.map((player, index) => {
                    return (
                        <Player
                            key={index}
                            name={player.name}
                            position={player.position}
                            isMe={player.position === props.playerPosition}
                            isLeader={player.is_leader}
                            isPaused={player.is_paused}
                            isNext={player.position === props.nextPosition}
                            quitHandler={props.quitHandler}
                            pauseToggleHandler={props.pauseToggleHandler}
                            isSeenFromLeadersPerspective={props.isSeenFromLeadersPerspective}
                        />
                    )
                })
            }
        </ol>
    )

}

export default PlayerList;