import React from 'react';
import Modal from 'react-bootstrap/Modal';
import Button from 'react-bootstrap/Button';

const Restart = (props) => {

    return (
        <Modal show={props.show} onHide={props.onHide} animation={false}>
            <Modal.Header closeButton>
                <Modal.Title>
                    Restart?
                </Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <p>
                   The cards will be reset, and turn goes to the first unpaused player.
                </p>
            </Modal.Body>
            <Modal.Footer>
                <Button variant="danger" onClick={props.yesAction}>Yes</Button>
                <Button variant="secondary" onClick={props.onHide}>No</Button>
            </Modal.Footer>
        </Modal>
    )
}

export default Restart;