import React from 'react';
import Modal from 'react-bootstrap/Modal';
import Button from 'react-bootstrap/Button';

const Quit = (props) => {

    return (
        <Modal show={props.show} onHide={props.onHide} animation={false}>
            <Modal.Header closeButton>
                <Modal.Title>
                    {props.isMe ? 'Quit' : `Kick ${props.name} out`}?
                </Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <p>
                    {props.isHost && props.isMe ?
                        'You are the host, if you quit then no one can restart this game or let players in.' :
                        (props.isMe ? 'You ' : `${props.name} `) +
                        'can rejoin later using the game code, if ' +
                        (props.isHost ? 'you (the host) go ' : 'the host goes ') +
                        'to the lobby.'
                    }
                </p>
            </Modal.Body>
            <Modal.Footer>
                <Button variant="danger" onClick={props.yesAction}>Yes</Button>
                <Button variant="secondary" onClick={props.onHide}>No</Button>
            </Modal.Footer>
        </Modal>
    )
}

export default Quit;