import React, { useState, useEffect } from 'react';
import './App.css';
import { getCookie, setCookie, clearGameCookies } from './Cookies';
import { gameExists, getGame, getGamePoll, createGame, joinGame, isValidField, canRestoreStateFromCookies, modifyPlayer, changeGameState, pickCard } from './ApiUtils';
import Home from './Home';
import Game from './Game';
import Lobby from './Lobby';
import Quit from './Quit';
import Pause from './Pause';
import Restart from './Restart';
import useInterval from './UseInterval';
import Terms from './Terms';
import CookiesModal from './CookiesModal';
import Sleep from './Sleep';

const App = ({ match }) => {

  const [playerId, setPlayerId] = useState(null);
  const [playerPosition, setPlayerPosition] = useState(null);
  const [game, setGame] = useState(null);

  const [quittingPlayerPosition, setQuittingPlayerPosition] = useState(null);
  const [pauseTogglingPlayerPosition, setPauseTogglingPlayerPosition] = useState(null);
  const [isConsideringRestart, setIsCondsideringRestart] = useState(false);

  const [isTermsShowing, setIsTermsShowing] = useState(false);
  const [hasAcceptedCookies, setHasAcceptedCookies] = useState(true);

  const [newPlayerName, setNewPlayerName] = useState('');
  const [newPlayerGameId, setNewPlayerGameId] = useState('');
  const [newPlayerJoinOrCreateGame, setNewPlayerJoinOrCreateGame] = useState('Join');
  const [newPlayerErrors, setNewPlayerErrors] = useState({});
  const [newPlayerSubmitDisabled, setNewPlayerSubmitDisabled] = useState(false);
  const [isWaitingOnNewplayerSubmitHandler, setIsWaitingOnNewplayerSubmitHandler] = useState(false);


  const [isPollingAsleep, setIsPollingAsleep] = useState(false);
  const [lastEventTime, setLastEventTime] = useState(new Date()/ 1000);

  const [localRandomPercent, setLocalRandomPercent] = useState(null);


  const changeHandler = (event) => {
    let val = event.target.value;
    switch (event.target.name) {
      case 'name':
        setNewPlayerName(val.slice(0, 16));
        break;
      case 'gameId':
        setNewPlayerGameId(val.slice(0, 6).toLowerCase());
        break;
      case 'joinOrCreate':
        setNewPlayerJoinOrCreateGame(val);
        break;
      default:
        console.log("Unexpected event for changeHandler");
    }
  }


  const newPlayerSubmitHandler = () => {
    const newPlayerSubmitErrors = {};
    if (newPlayerName.length < 1 || newPlayerName.length > 16) {
      newPlayerSubmitErrors.name = 'Name must be 1 to 16 characters'
    }
    if (newPlayerJoinOrCreateGame === 'Join' && !isValidField('gameId', newPlayerGameId)) {
      newPlayerSubmitErrors.gameId = 'Game code must be 6 letters'
    }
    if (!['Join', 'Create'].includes(newPlayerJoinOrCreateGame)) {
      newPlayerSubmitErrors.joinOrCreate = 'Choice of join or create is required';
    }
    setNewPlayerErrors(newPlayerSubmitErrors);
    if ('name' in newPlayerSubmitErrors || 'gameId' in newPlayerSubmitErrors || 'joinOrCreate' in newPlayerSubmitErrors) {
      setIsWaitingOnNewplayerSubmitHandler(false);
      return
    }
    setNewPlayerSubmitDisabled(true);
    if (newPlayerJoinOrCreateGame === 'Create') {
      createGame(newPlayerName)
        .then(createdGame => {
          setGame(createdGame.game);
          setPlayerId(createdGame.player.id);
          setPlayerPosition(createdGame.player.position);
          setIsPollingAsleep(false);
        })
        .catch(error => {
          setNewPlayerErrors({ ...newPlayerSubmitErrors, submit: 'Cannot create game' })
          setNewPlayerSubmitDisabled(false);
        });
    } else if (newPlayerJoinOrCreateGame === 'Join') {
      joinGame(newPlayerName, newPlayerGameId)
        .then(joinedGame => {
          setGame(joinedGame.game);
          setPlayerId(joinedGame.player.id);
          setPlayerPosition(joinedGame.player.position);
          setIsPollingAsleep(false);
        })
        .catch(error => {
          setNewPlayerErrors({ ...newPlayerSubmitErrors, submit: 'Cannot join game. The game code may be incorrect, or the game may not be in the lobby.' })
          setNewPlayerSubmitDisabled(false);
        });
    }
    setIsWaitingOnNewplayerSubmitHandler(false);
    return
  }

  const StartupModalIssues = {
    CookieGameNotFound: 1,
    UrlGameNotFound: 2,
    UrlAndCookieGameNotFound: 3,
    UrlGameNotFoundButCookieGameFound: 4,
    CookieGameNotFoundButUrlGameFound: 5,
    UrlVsCookieGame: 6
  }
  const [startupModalIssue, setStartupModalIssue] = useState(null);

  useEffect(() => {
    async function setup() {
      let retrievedGame = null;
      const cookies = {};
      for (let cookieName of ['hasAcceptedCookies', 'playerId', 'playerPosition', 'gameId']) {
        cookies[cookieName] = getCookie(cookieName);
      }
      cookies['playerPosition'] = parseInt(cookies['playerPosition']);

      if (cookies.hasAcceptedCookies === null)
        setHasAcceptedCookies(false);

      // Get value and validity of cookie/url gameIds
      const gameIds = {
        'cookie': {
          'value': cookies.gameId,
          'isValid': await canRestoreStateFromCookies(cookies)
        },
        'url': {
          'value': (typeof match.params.gameId) !== 'undefined' ? match.params.gameId : null,
        }
      }
      for (let source of ['cookie', 'url']) {
        gameIds[source].isPresent = gameIds[source].value !== null;
      }
      if (isValidField('gameId', gameIds.url.value))
        gameIds.url.isValid = await gameExists(gameIds.url.value);
      else
        gameIds.url.isValid = false;

      // Decide state of gameId and newPlayerGameId
      // if (gameIds.cookie.isValid && (gameIds.cookie.value === gameIds.url.value || !gameIds.url.isValid)) {
      if (gameIds.cookie.isValid) {
        setPlayerId(cookies.playerId);
        setPlayerPosition(cookies.playerPosition);
        retrievedGame = await getGame(gameIds.cookie.value);
        setGame(retrievedGame);
      }
      if (gameIds.url.isValid) {
        setNewPlayerGameId(gameIds.url.value);
      }

      // Which modals should show
      if (!gameIds.url.isPresent && gameIds.cookie.isPresent && !gameIds.cookie.isValid) {
        setStartupModalIssue(StartupModalIssues.CookieGameNotFound);
      }
      if (gameIds.url.isPresent && !gameIds.url.isValid) {
        if (!gameIds.cookie.isPresent) {
          setStartupModalIssue(StartupModalIssues.UrlGameNotFound);
        }
        else if (!gameIds.cookie.isValid) {
          setStartupModalIssue(StartupModalIssues.UrlAndCookieGameNotFound);

        } else {
          setStartupModalIssue(StartupModalIssues.UrlGameNotFoundButCookieGameFound);

        }
      }
      if (gameIds.url.isValid && gameIds.cookie.isPresent) {
        if (gameIds.cookie.isValid && gameIds.cookie.value !== gameIds.url.value) {
          setStartupModalIssue(StartupModalIssues.UrlVsCookieGame);
        }
        if (!gameIds.cookie.isValid) {
          setStartupModalIssue(StartupModalIssues.CookieGameNotFoundButUrlGameFound);
        }
      }
    }
    setup();
  }, [])


  function PollGame() {
    console.log("POLL");
    if (playerId === null) {
      if (game !== null) {
        setGame(null);
      }
    }
    if (game === null || isPollingAsleep) return; 
    if (new Date() / 1000 > lastEventTime + 120) {
      if (!isPollingAsleep) {
        setIsPollingAsleep(true);
      }
      return;
    }

    setNewPlayerSubmitDisabled(false);
    getGamePoll(game.id)
      .then(gamePoll => gamePoll.last_updated)
      .then(lastUpdated => {
        if (lastUpdated >= +lastEventTime) {
          setLastEventTime(lastUpdated);
          if (isPollingAsleep) return;
          getGame(game.id)
            .then(retrievedGame => {
              if (retrievedGame.picks.length !== game.picks.length) {
                setLocalRandomPercent(Math.floor(Math.random() * 100));
              }
              setLastEventTime(lastUpdated);
              return retrievedGame
            })
            .then(retrievedGame => {if (playerId !== null) { setGame(retrievedGame) }})
            .catch(error => { console.log("UNABLE TO RETRIEVE POLLED GAME") })
        }
      })
      .catch(error => {
        console.log("UNABLE TO POLL")
      })
  }

  const getPlayerId = () => {
    return playerId
  }


  const quit = (quittingPlayerPosition, playerPosition, playerId) => {
    setIsPollingAsleep(true);
    const oldPlayerId = playerId;
    setPlayerId(null);
    modifyPlayer(game.id, oldPlayerId, quittingPlayerPosition, 'quit')
      .then(updatedGame => {
        if (quittingPlayerPosition === playerPosition) {
          setGame(null);
          setIsPollingAsleep(true);
          setPlayerId(null);
          setPlayerPosition(null);
          clearGameCookies();
          setNewPlayerSubmitDisabled(false);
        } else {
          setGame(updatedGame);
        }
      })
      .catch(error => {
        setIsPollingAsleep(false);
        setPlayerId(oldPlayerId);
        console.log("UNABLE TO QUIT");
      });
    setQuittingPlayerPosition(null);
  }

  const pauseToggle = (pauseTogglingPlayerPosition, playerId) => {
    const pauseTogglingPlayer = game.players.filter(player => player.position === pauseTogglingPlayerPosition)[0];
    const action = pauseTogglingPlayer.is_paused ? 'unpause' : 'pause';
    modifyPlayer(game.id, playerId, pauseTogglingPlayerPosition, action)
      .then(updatedGame => {
        setGame(updatedGame);
      })
      .catch(error => {
        console.log("UNABLE TO QUIT");
      });
    setPauseTogglingPlayerPosition(null);
  }

  const toLobby = () => {
    changeGameState(game.id, playerId, 'to_lobby')
      .then(updatedGame => setGame(updatedGame))
      .catch(error => console.log("UNABLE TO LOBBY"));
  }

  const toGame = () => {
    changeGameState(game.id, playerId, 'to_game')
      .then(updatedGame => setGame(updatedGame))
      .catch(error => console.log("UNABLE TO GAME"));
  }

  const restart = () => {
    changeGameState(game.id, playerId, 'restart')
      .then(setIsCondsideringRestart(false))
      .then(updatedGame => setGame(updatedGame))
      .catch(error => console.log("UNABLE TO GAME"));
  }

  const pick = (cardIndex) => {
    if (playerPosition !== game.next_position) return
    pickCard(game.id, playerId, cardIndex)
      .then(updatedGame => setGame(updatedGame))
      .catch(error => console.log("UNABLE TO PICK"));
  }

  useInterval(() => {
    PollGame();
  }, 3000);

  return (
    <div className="App">
      {
        isTermsShowing && <Terms onHide={() => setIsTermsShowing(false)} show={isTermsShowing}/>
      }
      {
        !hasAcceptedCookies && <CookiesModal onHide={() => {setCookie('hasAcceptedCookies', 'true', 60 * 24); setHasAcceptedCookies(true)}} show={!hasAcceptedCookies} showTerms={() => setIsTermsShowing(true)}/>
      }
      {
            isPollingAsleep && game !== null && playerId !== null && <Sleep
              isAsleep={isPollingAsleep && game !== null}
              waitTime="2 minutes"
              wakeupClickHandler={() => {setIsPollingAsleep(false);  setLastEventTime(new Date() / 1000)}}
            />
          }
      {
        game !== null
        && playerPosition !== null
        && 'players' in game
        && game.players.some(player => player.position === quittingPlayerPosition)
        && 'name' in game.players.filter(player => player.position === quittingPlayerPosition)[0]
        && game.players.some(player => player.position === playerPosition)
        && 'is_leader' in game.players.filter(player => player.position === playerPosition)[0]
        &&
        <Quit
          show={quittingPlayerPosition !== null}
          onHide={() => setQuittingPlayerPosition(null)}
          yesAction={() => quit(quittingPlayerPosition, playerPosition, playerId)}
          position={quittingPlayerPosition}
          name={game.players.filter(player => player.position === quittingPlayerPosition)[0].name}
          isMe={playerPosition === quittingPlayerPosition}
          isHost={game.players.filter(player => player.position === playerPosition)[0].is_leader}
        />
      }

      {
        game !== null
        && playerPosition !== null
        && 'players' in game
        && game.players.some(player => player.position === playerPosition)
        && 'is_leader' in game.players.filter(player => player.position === playerPosition)[0]
        &&
        <Restart
          show={isConsideringRestart}
          onHide={() => setIsCondsideringRestart(false)}
          yesAction={() => restart()}
        />
      }

      {
        game !== null
        && playerPosition !== null
        && 'players' in game
        && game.players.some(player => player.position === pauseTogglingPlayerPosition)
        && 'name' in game.players.filter(player => player.position === pauseTogglingPlayerPosition)[0]
        && 'is_paused' in game.players.filter(player => player.position === pauseTogglingPlayerPosition)[0]
        && game.players.some(player => player.position === playerPosition)
        &&
        <Pause
          show={pauseTogglingPlayerPosition !== null}
          onHide={() => setPauseTogglingPlayerPosition(null)}
          action={game.players.filter(player => player.position === pauseTogglingPlayerPosition)[0].is_paused ? 'Unpause' : 'Pause'}
          position={pauseTogglingPlayerPosition}
          name={game.players.filter(player => player.position === pauseTogglingPlayerPosition)[0].name}
          isMe={playerPosition === pauseTogglingPlayerPosition}
          yesAction={() => pauseToggle(pauseTogglingPlayerPosition, playerId)}
          isHost={game.players.filter(player => player.position === playerPosition)[0].is_leader}
        />
      }

      {/* {!hasAcceptedCookies && <h1>Please accept cookies</h1>}
      <h1>APP</h1>
      {startupModalIssue === StartupModalIssues.CookieGameNotFound && <div>Could not load the game you were in. You may be able to rejoin it though.</div>}
      {startupModalIssue === StartupModalIssues.UrlGameNotFound && <div>This URL is for a game that does not exist.</div>}
      {startupModalIssue === StartupModalIssues.UrlAndCookieGameNotFound && <div>Could not load the last game you were in. Also, this url is for a game that does not exist.</div>}
      {startupModalIssue === StartupModalIssues.CookieGameNotFoundButUrlGameFound && <div>Could not load the last game you were in, the URL is good though.</div>}
      {startupModalIssue === StartupModalIssues.UrlGameNotFoundButCookieGameFound && <div>This URL is for a game that does not exist. Taking you to your last game instead.</div>}
      {startupModalIssue === StartupModalIssues.UrlVsCookieGame && <div>You are in a game already, but entered a URL for a different game. Want to stay in previous game or join new one?</div>}

      <p>playerId: {playerId}</p>
      <p>playerPosition: {playerPosition}</p>
      <p>newPlayerGameId: {newPlayerGameId}</p>
      <p>startupModalIssue: {startupModalIssue}</p> */}

      {
        game === null &&
        <Home
          gameId={newPlayerGameId}
          name={newPlayerName}
          joinOrCreate={newPlayerJoinOrCreateGame}
          changeHandler={changeHandler.bind(this)}
          prefix="newPlayerInput"
          errors={newPlayerErrors}
          submitDisabled={newPlayerSubmitDisabled}
          submitHandler={(event) => {event.preventDefault(); setIsWaitingOnNewplayerSubmitHandler(true); newPlayerSubmitHandler()}}
          showTerms={() => {setIsTermsShowing(true)}}
          isWaiting={isWaitingOnNewplayerSubmitHandler}
        />
      }
      {
        game !== null
        && Array.isArray(game.players)
        && game.players.some(player => player.position === playerPosition)
        && 'is_leader' in game.players.filter(player => player.position === playerPosition)[0]
        && 'is_paused' in game.players.filter(player => player.position === playerPosition)[0]
        &&
        (game.is_in_lobby ?
          <Lobby
            game={game}
            playerId={playerId}
            playerPosition={playerPosition}
            pauseToggleHandler={(position) => setPauseTogglingPlayerPosition(position)}
            quitHandler={(position) => setQuittingPlayerPosition(position)}
            restartHandler={() => setIsCondsideringRestart(true)}
            toGameHandler={() => toGame()}
            isLeader={game.players.filter(player => player.position === playerPosition)[0].is_leader}
          />
          :
          <Game
            game={game}
            localRandomPercent={localRandomPercent}
            playerPosition={playerPosition}
            quitHandler={(position) => setQuittingPlayerPosition(position)}
            pauseToggleHandler={(position) => setPauseTogglingPlayerPosition(position)}
            pickHandler={(cardIndex) => pick(cardIndex)}
            toLobbyHandler={() => toLobby()}
            isLeader={game.players.filter(player => player.position === playerPosition)[0].is_leader}
            isPaused={game.players.filter(player => player.position === playerPosition)[0].is_paused}
            restartHandler={() => setIsCondsideringRestart(true)}
          />
        )
      }

    </div>
  );
}

export default App;
