import React from 'react';
import PlayerList from './PlayerList';
import Button from 'react-bootstrap/Button';
import Container from 'react-bootstrap/Container';
import Col from 'react-bootstrap/Col';
import Row from 'react-bootstrap/Row';
import Spinner from './Spinner';

const Lobby = (props) => {
    return (
        <Container style={{ height: "200vh" }}>
            <Row>
                <Col className="d-none d-md-block" md={2} lg={3} xl={4}></Col>
                <Col sm={12} md={8} lg={6} xl={4} style={{ padding: "6px" }}>
                    <h1 style={{ textAlign: "center", fontWeight: "900" }}>Lobby</h1>
                    <div style={{ marginBottom: "20px" }}>
                        <p style={{ margin: "14px 0 2px 0" }}>Players can join this game by going to</p>
                        <code className="gameCode" style={{ fontSize: "calc(10px + 0.5vmin)" }}>https://tonytony99.github.io/roc/{props.game.id}</code>
                        <p style={{ margin: "8px 0 2px 0" }}>OR by using the code </p>
                        <code className="gameCode">{props.game.id}</code>
                    </div>
                    <Button variant="secondary" onClick={() => props.quitHandler(props.playerPosition)}>Quit</Button>
                    <Button variant="secondary" onClick={() => props.pauseToggleHandler(props.playerPosition)}>
                        {props.game.players.filter(player => player.position === props.playerPosition)[0].is_paused ? 'Unpause' : 'Pause'} me
                    </Button>
                    {!props.isLeader && <p style={{marginTop: "24px"}}><Spinner/> <strong>Waiting for host to close the lobby</strong></p>}
                    {props.isLeader &&
                        <div style={{ padding: "10px 10px 2px 10px", marginTop: "10px", border: "solid 2px #AAA", borderRadius: "5px" }}>
                            <h5>Host Options</h5>
                            <Button variant="success" onClick={() => props.toGameHandler()}>
                                To game
                            </Button>
                            <Button variant="secondary" onClick={() => props.restartHandler()}>
                                Restart
                            </Button>
                            <h5 style={{ margin: "14px 0 4px 0" }}>Players</h5>
                            <PlayerList
                                playerPosition={props.playerPosition}
                                players={props.game.players}
                                quitHandler={props.quitHandler}
                                pauseToggleHandler={props.pauseToggleHandler}
                                isSeenFromLeadersPerspective={props.isLeader}
                                nextPosition={props.game.next_position}
                            />
                        </div>}

                <h2 style={{marginTop: "20px"}}>Players</h2>
                <p>The lobby can be opened mid-game, by the host, to let more players in.</p>
                <PlayerList
                    playerPosition={props.playerPosition}
                    players={props.game.players}
                    quitHandler={props.quitHandler}
                    pauseToggleHandler={props.pauseToggleHandler}
                    isSeenFromLeadersPerspective={false}
                />
                </Col>
            </Row>
        </Container >
    )
}

export default Lobby;