import React from 'react';
import Modal from 'react-bootstrap/Modal';
import Button from 'react-bootstrap/Button';

const Pause = (props) => {

    return (
        <Modal show={props.show} onHide={props.onHide} animation={false}>
            <Modal.Header closeButton>
                <Modal.Title>
                    {props.action} {props.isMe ? ' yourself' : ` ${props.name}`}?
                </Modal.Title>
            </Modal.Header>

            <Modal.Body>
                <p>
                    {props.action === 'Pause'
                        ? (
                            props.isMe
                                ? `You will be skipped until you unpause yourself ${props.isHost ? '' : ' (or the host unpauses you)'}.`
                                : `${props.name} will be skipped until you unpause them (or they unpause themselves).`
                        )
                        : `${props.isMe ? 'You' : props.name} will no longer be skipped.`
                    }

                </p>
            </Modal.Body>
            <Modal.Footer>
                <Button variant="danger" onClick={props.yesAction}>Yes</Button>
                <Button variant="secondary" onClick={props.onHide}>No</Button>
            </Modal.Footer>
        </Modal>
    )
}

export default Pause;