import { setCookie } from './Cookies';

const regexes = {
    'gameId': '^[a-z]{6}$',
    'playerPosition': '^[0-9]{1,4}$',
    'playerId': '^[a-fA-F0-9]{8}-([a-fA-F0-9]{4}-){3}[a-fA-F0-9]{12}$'
}

export const isValidField = (fieldName, value) => {
    if (!(fieldName in regexes)) throw new Error('Validator not found for supplied fieldName')
    return RegExp(regexes[fieldName]).test(value);
}

// const apiUrl = "http://127.0.0.1:8000/";
const apiUrl = "https://fathomless-river-20245.herokuapp.com/"
const gameCookieDurationMinutes = 4 * 60;

export async function getData(endpoint) {
    const response = await fetch(apiUrl + endpoint)
        .then(response => {
            if ([400, 404, 405, 500].includes(response.status)) throw new Error(response.status);
            return response
        });
    return response
}

export async function getGamePollResponse(gameId) {
    const response = await getData(`games/${gameId}/poll/`);
    return response
}

export async function getGamePoll(gameId) {
    const response = await getGamePollResponse(gameId);
    const gamePoll = await response.json();
    return gamePoll
}


export async function getGameResponse(gameId) {
    const response = await getData(`games/${gameId}/`);
    return response
}

export async function getGame(gameId) {
    const response = await getGameResponse(gameId);
    const game = await response.json();
    return game
}

export async function gameExists(gameId) {
    try {
        const response = await getGameResponse(gameId);
        console.log(response.status);
        return true
    }
    catch (error) {
        console.log(error);
        return false
    }
}

export async function canRestoreStateFromCookies(cookiesObject) {
    const cookieNames = ['playerId', 'playerPosition', 'gameId'];
    for (let cookieName of cookieNames) {
        if (!(cookieName in cookiesObject)) return false;
        if (!isValidField(cookieName, cookiesObject[cookieName])) return false;
    }
    try {
        const game = await getGame(cookiesObject['gameId']);
        if (!('players' in game)) return false;
        if (!game.players.some(player => player.position === cookiesObject['playerPosition'])) return false
    } catch (error) {
        return false
    }
    return true
}

export async function sendData(endpoint, data, method = 'POST') {
    const response = await fetch(apiUrl + endpoint, {
        method: method,
        headers: {
            'Content-Type': 'application/json'
        },
        body: JSON.stringify(data)
    }).then(response => {
        if (![200, 201].includes(response.status)) throw new Error(response.status);
        return response
    });
    return response
}

export async function postGameResponse() {
    console.log("postGameResposne()");
    const response = await sendData("games/", {});
    return response
}

export async function postGame() {
    console.log("postGame()");
    const response = await postGameResponse();
    const game = await response.json();
    return game
}

export async function postPlayerResponse(name, gameId) {
    const response = await sendData("players/", {
        name: name,
        game: gameId
    });
    return response
}

export async function postPlayer(name, gameId) {
    const response = await postPlayerResponse(name, gameId);
    if (response.status !== 201) return null
    const player = await response.json();
    return player
}

export async function createGame(playerName) {

    try {
        const newGame = await postGame();
        const newPlayer = await postPlayer(playerName, newGame.id);
        const updatedGame = await getGame(newGame.id);
        setCookie('gameId', updatedGame.id, gameCookieDurationMinutes);
        setCookie('playerId', newPlayer.id, gameCookieDurationMinutes);
        setCookie('playerPosition', newPlayer.position, gameCookieDurationMinutes);
        return { game: updatedGame, player: newPlayer }
    } catch (error) {
        throw error;
    }
}

export async function joinGame(playerName, gameId) {
    const newPlayer = await postPlayer(playerName, gameId);
    if (newPlayer === null) return 'error'
    const updatedGame = await getGame(gameId);
    setCookie('gameId', updatedGame.id, gameCookieDurationMinutes);
    setCookie('playerId', newPlayer.id, gameCookieDurationMinutes);
    setCookie('playerPosition', newPlayer.position, gameCookieDurationMinutes);
    return { game: updatedGame, player: newPlayer }
}


export async function patchGameResponse(gameId, data) {
    const response = await sendData(`games/${gameId}/`, data, 'PATCH');
    return response
}

export async function patchGame(gameId, data) {
    try {
        const response = await patchGameResponse(gameId, data);
        const game = await response.json();
        return game
    } catch (error) {
        throw error;
    }
}

export async function modifyPlayer(gameId, playerId, playerPosition, action) {
    if (!['pause', 'unpause', 'quit'].includes(action)) {
        console.log("INVALID PLAYER MODIFICATION");
        return
    }
    const patchData = { player_id: playerId, action: action, position: playerPosition };
    const updatedGame = await patchGame(gameId, patchData);
    return updatedGame
}


export async function changeGameState(gameId, playerId, action) {
    if (!['to_game', 'to_lobby', 'restart'].includes(action)) {
        console.log("INVALID GAME MODIFICATION");
        return
    }
    const patchData = { player_id: playerId, action: action };
    const updatedGame = await patchGame(gameId, patchData);
    return updatedGame
}

export async function pickCard(gameId, playerId, cardIndex) {
    const patchData = { player_id: playerId, card_index: cardIndex, action: 'pick_card' };
    const updatedGame = await patchGame(gameId, patchData);
    return updatedGame
}