import React from 'react';
import Container from 'react-bootstrap/Container';
import Col from 'react-bootstrap/Col';
import Row from 'react-bootstrap/Row';
import Rule from './Rule';
import Board from './Board';
import PlayerList from './PlayerList';
import Button from 'react-bootstrap/Button';
import Alert from 'react-bootstrap/Alert';

const Game = (props) => {
    return (
        <Container style={{ height: "200vh" }}>
            <Row>
                <Col className="d-none d-lg-block" lg={12} xl={12}>
                    <div style={{ height: "10vh" }}></div>
                </Col>
                <Col className="d-none d-md-block" md={3} lg={2} xl={2}></Col>
                <Col sm={12} md={6} lg={5} xl={5} style={{ textAlign: "center", padding: "6px" }}>
                    <Board
                        game={props.game}
                        sideLength={260}
                        radius={140}
                        sidePadding={14}
                        cardWidth={50}
                        cardHeight={70}
                        cardCornerRadius={3}
                        onCardClickHandler={props.pickHandler}
                    />
                    {
                        props.game.latest_card_index &&
                        < Rule
                            localRandomPercent={props.localRandomPercent}
                            globalRandomPercent={props.game.percents[props.game.latest_card_index]}
                            playerName={
                                props.game.players.some(player => player.position === props.game.latest_position) ?
                                    props.game.players.filter(player => player.position === props.game.latest_position)[0].name :
                                    '[PLAYER HAS LEFT]'

                            }
                            cardIndex={props.game.latest_card_index}
                        />
                    }
                    {
                        props.playerPosition === props.game.next_position &&
                        <Alert style={{ marginTop: "20px" }} variant="dark">
                            <strong>You are next to pick a card</strong>
                        </Alert>
                    }
                </Col>
                <Col md={3} lg={0} className="d-lg-none d-xl-none" />
                <Col md={3} lg={0} className="d-lg-none d-xl-none" />
                <Col sm={12} md={6} lg={5} xl={5} style={{ padding: "6px" }}>
                    <div style={{ borderRadius: ".25rem", border: " solid 2px rgba(255,255,255,0.0)" }} >
                        <h4 style={{ fontWeight: 900, paddingTop: "3vmin", marginBottom: "0", paddingBottom: "10px" }}>Players</h4>
                        <PlayerList
                            playerPosition={props.playerPosition}
                            players={props.game.players}
                            quitHandler={props.quitHandler}
                            pauseToggleHandler={props.pauseToggleHandler}
                            isSeenFromLeadersPerspective={false}
                            nextPosition={props.game.next_position}
                        />
                        <br />
                        <Button variant="secondary" onClick={() => props.pauseToggleHandler(props.playerPosition)}>
                            {props.isPaused ? 'Un-Pause' : 'Pause'} me
                        </Button>
                        <Button variant="secondary" onClick={() => props.quitHandler(props.playerPosition)}>
                            Quit
                        </Button>

                        {props.isLeader && <div style={{ padding: "10px", marginTop: "10px", border: "solid 2px #AAA", borderRadius: "5px" }}>
                            <h5>Host Options</h5>
                            <Button variant="secondary" onClick={() => props.toLobbyHandler()}>
                                Go To Lobby
                        </Button>
                            <Button variant="secondary" onClick={() => props.restartHandler()}>
                                Restart
                        </Button>
                            <h5 style={{ margin: "14px 0 4px 0" }}>Players</h5>
                            <PlayerList
                                playerPosition={props.playerPosition}
                                players={props.game.players}
                                quitHandler={props.quitHandler}
                                pauseToggleHandler={props.pauseToggleHandler}
                                isSeenFromLeadersPerspective={props.isLeader}
                                nextPosition={props.game.next_position}
                            />
                        </div>
                        }

                    </div>
                </Col>
            </Row>
        </Container >
    )
}


export default Game;