import React from 'react';
import Container from 'react-bootstrap/Container';
import Col from 'react-bootstrap/Col';
import Row from 'react-bootstrap/Row';
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import Alert from 'react-bootstrap/Alert';
import Spinner from './Spinner';

const Home = (props) => {
    return (
        <Container style={{ height: '100vh' }}>
            <Row>
                <Col md={2} lg={3} xl={4}></Col>
                <Col sm={12} md={8} lg={6} xl={4} style={{ textAlign: "center" }}>
                    <h1 style={{ fontWeight: 900, paddingTop: '12vmin', paddingBottom: '0' }}>Ring of Cards</h1>
                </Col>
                <Col md={2} lg={3} xl={4}></Col>
            </Row>

            <Row>
                <Col xs={0} md={2} lg={3} xl={4}></Col>
                <Col xs={12} sm={10} md={8} lg={6} xl={4}>
                    <Form noValidate onSubmit={props.submitHandler}>
                        <Form.Group controlId={props.prefix + 'Name'} style={{ paddingTop: '8vh' }}>
                            <Form.Label>Name</Form.Label>
                            <Form.Control
                                placeholder='Pick a name'
                                name={'name'}
                                value={props.name}
                                onChange={props.changeHandler}
                                maxLength='16'
                                required
                            />
                            {
                                'name' in props.errors && (props.name.length < 1 || props.name.length > 16) &&
                                <Alert className="validationError" variant="danger">
                                    {props.errors.name}
                                </Alert>
                            }
                        </Form.Group>

                        <Form.Group controlId={props.prefix + 'JoinOrCreate'} style={{ paddingTop: '1vh' }}>
                            {
                                [{ value: 'Join', label: 'Join a game' }, { value: 'Create', label: 'Create a new game' }].map((option) => {
                                    return <Form.Check
                                        type='radio'
                                        id={'radio-' + option.value}
                                        key={props.prefix + option.value}
                                        label={option.label}
                                        value={option.value}
                                        checked={props.joinOrCreate === option.value}
                                        name={'joinOrCreate'}
                                        onChange={props.changeHandler}
                                    />
                                })
                            }
                            {
                                'joinOrCreate' in props.errors &&
                                <Form.Text className="error">
                                    {props.errors.joinOrCreate}
                                </Form.Text>
                            }
                        </Form.Group>
                        <Form.Group
                            controlId={props.prefix + 'gameId'}
                            style={{
                                paddingTop: '1vh',
                                display: props.joinOrCreate === 'Join' ? 'block' : 'none'
                            }}
                        >
                            <Form.Label>Game Code</Form.Label>
                            <Form.Control
                                placeholder='6 letters'
                                name={'gameId'}
                                value={props.gameId}
                                onChange={props.changeHandler}
                                maxLength='6'
                            />
                            {
                                'gameId' in props.errors &&
                                <Alert className="validationError" variant="danger">
                                    {props.errors.gameId}
                                </Alert>

                            }
                        </Form.Group>
                        <Form.Group controlId='formSubmit' style={{ paddingTop: '1vh' }}>
                            <Button variant='success' type='submit' disabled={props.submitDisabled}>
                                {props.joinOrCreate}
                            </Button>
                            {
                                props.isWaiting && <Spinner />
                            }

                            {
                                'submit' in props.errors &&
                                <Alert className="validationError" variant="danger">
                                    <strong>Error</strong> {props.errors.submit}
                                </Alert>
                            }
                        </Form.Group>

                    </Form>
                    <p>
                        By playing you agree to the
                                <button style={{ border: 'none', backgroundColor: 'rgba(0,0,0,0)', color: '#8af' }} onClick={props.showTerms}>
                            terms and privacy policy
                                </button>
                                and that you are at least 21 years of age. Always play responsibly, water is recommended as a drink.
                           </p>
                </Col>
            </Row>
        </Container>
    )
}

export default Home;