import React from 'react';
import Badge from 'react-bootstrap/Badge';
import Button from 'react-bootstrap/Button';

const Player = (props) => {
    return (
        <li style={{marginTop: "10px"}}>
            {
                props.isSeenFromLeadersPerspective &&
                <React.Fragment>
                    <Button className="hostPlayerOption" variant="secondary" onClick={() => props.quitHandler(props.position)}>
                        Remove
                    </Button>
                    <Button className="hostPlayerOption" variant="secondary" onClick={() => props.pauseToggleHandler(props.position)}>
                        {props.isPaused ? 'Unpause' : 'Pause'}
                    </Button>
                </React.Fragment>
            }
            {props.name}
            {!props.isSeenFromLeadersPerspective &&
                <span>
                    {props.isMe ?
                        <span className="badgeContainer">
                            <Badge pill variant="warning" >
                                me
                </Badge>
                        </span> : null}
                    {props.isLeader ?
                        <span className="badgeContainer">
                            <Badge pill variant="secondary" title="Hosts can restart games, open lobbies, and pause players">
                                host
                </Badge>
                        </span> : null}
                    {props.isPaused ?
                        <span className="badgeContainer">
                            <Badge pill variant="secondary" title="While paused, this players turns are skipped">
                                paused
                </Badge>
                        </span> : null}
                    {props.isNext ?
                        <span className="badgeContainer">
                            <Badge pill variant="success" title="This player is about to pick a card">
                                picking
                </Badge>
                        </span> : null}
                </span>
            }
        </li>
    )

}

export default Player;